
/**
 * 
 */
export default {
	install(Vue, options) {
		Vue.prototype.fn = {
			/**
			 * 
			 * @method warn
			 * @param  {string} msg 
			 */
			warn: function(msg) {
				console.error(msg);
			},
			/**
			 * 
			 * @param  {object} _this  Vue
			 * @param  {object} params ID
			 */
			gotoConversation: function(_this, params) {
				_this.$router.push({ 
					path: '/show-panel/conversation',
					query: { 
						friendID: params.friendID, 
						name: params.name,
						account: params.account
					}
				});
			}
		}
	}
}


